@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Karla:wght@400;700&display=swap);
/* Header*/
.header {
    display: flex;
    place-items: center;
    height: 5rem;
    padding: 0 1.5rem;
    background-color: white;
}

.header svg {
    margin-right: auto;
}

.header button {
    height: 2rem;
    width: 2rem;
    font-size: 1.1rem;
    border: 1px solid hsl(270, 9%, 17%);
    color: hsl(270, 9%, 17%);
}

/*Navigation*/
.nav {
    position: absolute;
    top: 5rem;
    width: 100%;
    height: calc(100vh - 5rem);
    background-color: hsl(270, 9%, 17%);
    text-align: center;
    padding-top: 1rem;
    transition: 0.5s;
    
}

.nav:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 13.5rem;
    background: url(/FEM-Insure/static/media/mobile-nav.2922ed2c.svg);
    background-repeat: no-repeat;
    background-size: 100% 13.5rem;
}

nav li {
    margin-top: 1rem;
    display: flex;
    place-items: center;
}

nav li a {
    letter-spacing: 1px;
    width: 100%;
    padding: 1rem;
    color: white;
}

nav li a:active {
    border: 2px solid white;
}

.showNav {
    transform: translateX(0);
}

.hideNav {
    transform: translateX(calc(-100% - 80px));
}
.button {
    border: 1.5px solid white;
    background-color: transparent;
    padding: 0.7rem 2rem;
    color: white;
    letter-spacing: 1px;
    font-size: 0.8rem;
}
.hero {
    background-color: hsl(256, 26%, 20%);
    text-align: center;
    color: white;
    letter-spacing: 1.5px;
}

.hero img {
    width: 100%;
}

.heroIntro {
    padding: 6rem 1.5rem;
    position: relative;
}

.heroIntro:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 10rem;
    width: 100%;
    background: url(/FEM-Insure/static/media/bg-pattern-intro-left-mobile.562149a8.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.heroIntro:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -22rem;
    height: 100%;
    width: 7.5rem;
    background: url(/FEM-Insure/static/media/bg-pattern-intro-right-mobile.ef3d23bd.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.heroIntro p {
    margin: 2rem 0;
}
.mainCard {
    padding: 2rem 0;
}

.mainCard h2 {
    padding: 2rem 0 1rem 0;
}
.whyUs {
    text-align: center;
    padding: 7rem 1.5rem 0 1.5rem;
}

.whyUs hr {
    width: 40%;
    margin: auto;
}

.whyUs h1 {
    margin-top: 2.5rem;
}
.howWeWork {
    background-color: hsl(256, 26%, 20%);
    color: white;
    text-align: center;
    padding: 3.5rem 1rem;
    margin:5rem 1.5rem 7rem;
    position: relative;
}

.howWeWork:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 90%;
    width: 50%;
    background: url(/FEM-Insure/static/media/bg-pattern-how-we-work-mobile.a4de14b0.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.howWeWork h1 {
    margin-bottom: 2rem;
}
header {
    position: fixed;
    width: 100vw;
    z-index: 1000;
}
/*Resets*/

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    outline: none;
    list-style: none;
    letter-spacing: 1px;
}

:root {
    font-size: 16px;
}

/*General Styles*/

#root {
    display: grid;
    grid-template-rows: auto 1fr;
}

h1, h2, h3 {
    font-family: "DM Serif Display", serif;
}

h1 {
    font-size: 2.5rem;
    font-weight: 400;
    line-height: 1;
}

p {
    font-size: 0.9rem;
    line-height: 1.7;
    text-align: justify;
}

body {
    font-family: "Karla", sans-serif;
}
